<template>
  <div id="resgister">
    <div id="div">
        <div id="header"></div>
            <div id="user">
                <div class="inp"> &nbsp;&nbsp;&nbsp; <input type="text"  placeholder="" v-model="obj.username">
                </div>
                <div class="inp"> &nbsp;&nbsp;&nbsp; <input type="password" placeholder="" v-model="obj.password"></div>
                <div class="inp"><input type="text" placeholder="" v-model="obj.phone_number"></div>
                <div class="inp"><input type="text" placeholder="" v-model="obj.Invitation_code"></div>
            </div>
            <div id="btn">
                <button @click="add" class="btn-reg"></button>
                <button class="btn-reg" style="background:#555" @click="cz"></button>
            </div>
    
    </div>
        
  
  </div>
</template>

<script>
export default {
    data(){
        return {
            obj:{
                username:'',
                password:'',
                phone_number:'',
                Invitation_code:''
            },
        }
    },
    methods:{
        cz(){
            this.obj.password=''
            this.obj.Invitation_code=''
            this.obj.phone_number=''
            this.obj.username=''
        },
        async add(){
            let data = {...this.obj}
           
            
            let b =  Object.keys(data).findIndex(item=>{
                return data[item]==''
                })
            if(b!=-1) {
                 this.$message({
                    message: '',
                    type: 'warning'
                    });
                return
            }
            if(data.Invitation_code==37211){
                delete data.Invitation_code
                const a = await this.$http.post('/owner/register',data)
                console.log(a);
                if (a.data.code==200){
                   
                    this.$message({
                        message: '',
                        type: 'success'
                        });
                    this.$router.push('/sgin')
                }else{
                    this.$message.error(`${a.data.msg}`);
                    return
                }
            }else{
                 this.$message('');
            }
            this.obj.password=''
            this.obj.Invitation_code=''
            this.obj.phone_number=''
            this.obj.username=''
        }
    }
}
</script>

<style>
#btn{
    display: flex;
    justify-content: space-evenly;
}
.btn-reg{
  margin-top: 20px;
  width: 30%;
  border: none;
  height: 40px;
  color: #fff;
  font-size: 17px;
  border-radius:3px ;
  background-color: #61daff;
}
#div{
    width: 400px;
  position: absolute;
  right: 22%;
  top: 22%;
}
#resgister{
    width: 100%;
  height:100% ;
  background-size: 90% 100%;
  background-image: url(../../assets/beijing.png);  
  background-repeat: no-repeat;
  background-color: #f2f6f9;
  position: relative;
}
#user{
  width:80% ;
  margin: 0 auto;
  font-size: 14px;
  font-family:fantasy;
}
.inp{
  padding-top: 10px;
  padding-bottom: 10px;
  margin: 20px 0;
  margin-left: 10px;
  color: #7f8182;
  border-bottom: 1px solid #7f8182;
}
.inp>input{
  border: none;
  outline:none;
  background-color: transparent;
}
</style>